const validate = values => {
    const errors = {};
    const { name, comment } = values;
    if (!name) {
        errors.name = 'Vui lng nhp tn';
    } else if (name.trim() && name.length < 1) {
        errors.name = 'Tn phi t 8 k t tr ln';
    }

    if (!comment) {
        errors.comment = 'Vui lng nhp bnh lun';
    } else if (comment.trim() && comment.length < 1) {
        errors.comment = 'Bnh lun phi t 16 k t tr ln';
    }
    return errors;
};

export default validate;